a {
  font-style: inherit;
  color: inherit;
  text-decoration: none;
  display: block;
}

p {
  margin: 0;
}

ul {
  margin: 0;
  padding-left: 0;
}

img {
  width: 100%;
  display: block;
}

.container {
  width: 1158px;
  margin: 0 auto;
  padding-left: 15px;
  padding-right: 15px;
}

.header {
  text-align: center;
  background-color: #2f0840;
  justify-content: center;
  max-width: 1440px;
  margin: 0 auto;
  padding-top: 20px;
  padding-bottom: 20px;
  display: flex;
}

input {
  border: none;
  border-radius: 12px;
  width: 325px;
  line-height: 2.15;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

input:focus {
  outline-color: #2f0840;
}

.searh-btn {
  color: #2f0840;
  letter-spacing: .04em;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 12px;
  min-width: 76px;
  height: 34px;
  margin-left: 15px;
  font-size: 16px;
  font-style: normal;
  font-weight: 400;
  line-height: 1.5;
}

.searh-btn:-webkit-any(:hover, :focus) {
  color: #fff;
  background-color: #ca9bde;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.searh-btn:is(:hover, :focus) {
  color: #fff;
  background-color: #ca9bde;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.gallery-section {
  margin-top: 15px;
  margin-bottom: 15px;
}

.gallery {
  flex-wrap: wrap;
  display: flex;
}

.lightbox {
  cursor: pointer;
  width: calc(33.3333% - 16px);
  margin-bottom: 24px;
  margin-right: 24px;
}

.lightbox:nth-child(3n) {
  margin-right: 0;
}

.photo-card {
  border-radius: 4px;
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08);
}

.photo-card:-webkit-any(:hover, :focus) {
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo-card:is(:hover, :focus) {
  box-shadow: 0 1px 6px rgba(46, 47, 66, .08), 0 1px 1px rgba(46, 47, 66, .16), 0 2px 1px rgba(46, 47, 66, .08);
}

.photo-card img {
  object-fit: cover;
  height: 250px;
  margin: 0 auto;
}

.lightbox-cover-wrap {
  position: relative;
  overflow: hidden;
}

.info {
  color: #fff;
  letter-spacing: .02em;
  background-color: rgba(121, 23, 138, .5);
  flex-wrap: wrap;
  justify-content: space-around;
  width: 100%;
  height: 22%;
  padding: 14px;
  font-size: 12px;
  line-height: 1;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  bottom: 0;
  transform: translateY(100%);
}

.photo-card:-webkit-any(:hover, :focus) .info {
  transform: translateY(0);
}

.photo-card:is(:hover, :focus) .info {
  transform: translateY(0);
}

.info-item {
  flex-direction: column;
  align-items: center;
  gap: 3.5px;
  display: flex;
}

.load-more {
  color: #2f0840;
  letter-spacing: .04em;
  cursor: pointer;
  background-color: #fff;
  border: none;
  border-radius: 4px;
  min-width: 200px;
  height: 45px;
  margin: 0 auto;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 1.5;
  display: none;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more:-webkit-any(:hover, :focus) {
  color: #fff;
  background-color: #ca9bde;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

.load-more:is(:hover, :focus) {
  color: #fff;
  background-color: #ca9bde;
  box-shadow: 0 4px 4px rgba(0, 0, 0, .15);
}

